import { Component, OnInit, Input } from '@angular/core';
import { UsersService } from "../users.service"

@Component({
  selector: 'app-listing',
  templateUrl: './listing.component.html',
  styleUrls: ['./listing.component.css']
})
export class ListingComponent implements OnInit {

  //when attribute value changes in parent component(content), it will be received by child(listing) component
//for serive, comment the below
  // @Input("listing") listing : any; //"listing": name in which input is being received

  compListing : any;
  constructor(private _UsersService : UsersService) { 
    // this.listing = this._UsersService.getData();
    this._UsersService.getData()
    .subscribe((data)=>{
    this.compListing = data;
    
  })
  }

  ngOnInit() {
    console.log("list is",this.compListing);
  }

}
