import { Injectable } from '@angular/core';
import { BehaviorSubject} from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class UsersService {

 listing = [{
    user: "Earphone",
    price: "200"
  }, {
    user: "Mug",
    price: "300"
  }, {
    user: "Photo Frame",
    price: "300"
  }]

compListing = new BehaviorSubject(this.listing);  //default data is to be given when made through behaviour subject. lIting is now observable

updateData() {
  this.compListing.next([]);
}

getData() {
  return this.compListing;
}

changeName(name, newPrice) {
  this.compListing.next([{
    user: name,
    price: newPrice
  }])
}

  constructor() { }
}
